<template lang="html">
  <div class="Add-contract">
    <Row type="flex" justify="space-around" class="code-row-bg search_criteria">
        <Col span="6" class="font-16">
          <h3>{{$t("common.Add_contract")}}</h3>
        </Col>
        <Col span="5">

        </Col>
        <Col span="5">

        </Col>
        <Col span="8">
          <Button type="primary" class="mgl-10">{{$t("common.Preview")}}</Button>
          <Button type="info" class="mgl-20" @click="succ" v-if="!isban">{{$t("common.Save")}}</Button>
          <Button type="warning" class="mgl-20" @click="cancel">{{$t("common.Cancel")}}</Button>
        </Col>
    </Row>
    <Row type="flex" justify="start" class="code-row-bg contract-row">
      <Col span="4" class="text-right">{{$t("common.Contract_name")}}：</Col>
      <Col :md="18" :lg="14" class="mgl-20"> <Input v-model="ContractName" placeholder="" ></Input></Col>
    </Row>
    <Row type="flex" justify="start" class="code-row-bg contract-row">
      <Col span="4" class="text-right">{{$t("common.Contract_use")}}：</Col>
      <Col :md="18" :lg="14" class="mgl-20"> <Input v-model="ContractUse" type="textarea" :rows="4" placeholder="" ></Input></Col>
    </Row>
    <Row type="flex" justify="start" class="code-row-bg text-edit">
      <Col span="4" class="text-right">{{$t("common.Contract_content")}}：</Col>
      <Col :md="18" :lg="14" class="mgl-20">
        <div ref="editor" style="text-align:left"><p>{{ editorContent }}</p></div>
        <!-- <button @click="getContent">查看内容</button> -->
      </Col>
    </Row>
    
  </div>
</template>

<script>
import E from 'wangeditor'
export default {
  data (){
    return {
      ContractName:'',
      ContractUse:'',
      editorContent: '',
      isban:null
    }
  },
  methods:{
    succ () {
      this.$Message.success(this.$t("common.Save_success"));
      setTimeout(() =>{
        this.$router.push('/User_contract');
      },2000)
    },
    getContent: function () {
        alert(this.editorContent)
    },
    getParams () {
        // 取到路由带过来的参数
        let routerParams = this.$route.params.send_data
        // 将数据放在当前组件的数据内
        if(routerParams){
          this.ContractName = routerParams.name
          this.ContractUse = routerParams.use
          this.editorContent = routerParams.content
          this.isban = routerParams.ban
        }
    },
    cancel () {
      this.$router.push('/User_contract');
    }
  },
  mounted() {
    var editor = new E(this.$refs.editor)
    editor.customConfig.onchange = (html) => {
      //this.editorContent = html
    }
    editor.create()
    this.getParams ()
    if(this.isban == true){
      editor.$textElem.attr('contenteditable', false)
    }

  }
}
</script>

<style lang="css">
.Add-contract{
  background-color: #fff;
  padding: 20px;
}
.Add-contract .search_criteria{
  height: 50px;
  background-color: rgba(242, 242, 242, 1);
  line-height: 50px;
  text-align: center;
}
.Add-contract .contract-row{
  line-height: 50px;
  margin-bottom: 20px;
}
.Add-contract .ivu-select-dropdown{
  z-index: 99999
}
</style>



